import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Box, Card, CardContent, Typography, Grid } from '@mui/material';
import c3 from 'c3';
import 'c3/c3.css';
import * as d3 from 'd3';
import Chart from 'chart.js/auto';

const Dashboard = () => {
    const [weatherData, setWeatherData] = useState(null);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await axios.get('http://localhost:5000/api/weather');
                setWeatherData(response.data);
            } catch (err) {
                console.error('Error fetching weather data:', err.message);
                setError(err.message);
            }
        };

        fetchData();
    }, []);

    //Chance of Rain
    const renderC3BarChart = (weatherData, dates) => {
        const originalRain = weatherData.originalData?.forecast?.forecast.forecastday.map((day) => day.day.daily_chance_of_rain) || [];
        const newRain = weatherData.newData?.forecast?.forecast.forecastday.map((day) => day.day.daily_chance_of_rain) || [];
        c3.generate({
            bindto: '#weatherChart',
            data: {
                columns: [
                    [`Instance ${weatherData?.originalData?.instanceId || 'Original'}`, ...originalRain],
                    [`Instance ${weatherData?.newData?.instanceId || 'New'}`, ...newRain],
                ],
                type: 'bar',
            },
            axis: {
                x: {
                    type: 'category',
                    categories: dates,
                    label: 'Dates',
                },
                y: {
                    label: 'Chance of Rain (%)',
                },
            },
            legend: {
                position: 'right',
            },
            tooltip: {
                grouped: true,
            },
        });
    };

    //Heatmap
    const generateHeatmap = (id, heatmapData, days, hours) => {
        d3.select(`#${id} svg`).remove();

        const svg = d3.select(`#${id}`).append('svg')
            .attr('width', 700)
            .attr('height', 600);

        const xScale = d3.scaleBand().domain(days).range([0, 700]).padding(0.05);
        const yScale = d3.scaleBand().domain(hours).range([0, 600]).padding(0.05);

        const [minTemp, maxTemp] = d3.extent(heatmapData, d => d.temperature);
        const colorScale = d3.scaleSequential()
            .domain([minTemp || 0, maxTemp || 30])
            .interpolator(d3.interpolateBlues);

        svg.selectAll('rect')
            .data(heatmapData)
            .enter()
            .append('rect')
            .attr('x', d => xScale(d.day))
            .attr('y', d => yScale(d.hour))
            .attr('width', xScale.bandwidth())
            .attr('height', yScale.bandwidth())
            .attr('fill', d => colorScale(d.temperature))
            .append('title')
            .text(d => `Temp: ${d.temperature}°C`);

        svg.append('g').attr('transform', 'translate(0,350)').call(d3.axisBottom(xScale));
        svg.append('g').call(d3.axisLeft(yScale));
    };

    //Humidity Chart

    const renderHumidityChart = (elementId, weatherData, label, chartInstances) => {
        if (chartInstances[elementId]) {
            chartInstances[elementId].destroy();
        }
        const ctx = document.getElementById(elementId)?.getContext('2d');
        if (!ctx) return;
    
        const chartData = {
            labels: weatherData?.days || [], // X-axis labels
            datasets: [
                {
                    label: label,
                    data: weatherData?.humidity || [], // Corrected data for Y-axis
                    borderColor: 'rgba(75, 192, 192, 1)',
                    backgroundColor: 'rgba(75, 192, 192, 0.2)',
                    tension: 0.4,
                },
            ],
        };
    
        chartInstances[elementId] = new Chart(ctx, {
            type: 'line',
            data: chartData, // Correctly assign the data object
            options: {
                responsive: true,
                scales: {

                    x: { title: { display: true, text: 'Days' } },
                    y: { title: { display: true, text: 'Humidity (%)' } },
                },
            },
        });
    };
    

    const chartInstances = {};

    useEffect(() => {
        if (weatherData) {
            const dates = weatherData?.originalData?.forecast?.forecast.forecastday?.map(day => day.date) || [];
            renderC3BarChart(weatherData, dates);

            const heatmapDataOriginal = weatherData?.originalData?.forecast?.forecast.forecastday.flatMap(day =>
                day.hour.map(hour => ({
                    day: day.date,
                    hour: new Date(hour.time).getHours(),
                    temperature: hour.temp_c || 0,
                }))
            );
            const heatmapDataNew = weatherData?.newData?.forecast?.forecast.forecastday.flatMap(day =>
                day.hour.map(hour => ({
                    day: day.date,
                    hour: new Date(hour.time).getHours(),
                    temperature: hour.temp_c || 0,
                }))
            );

            const days = dates;
            const hours = Array.from({ length: 24 }, (_, i) => i);
            generateHeatmap('originalDataHeatmap', heatmapDataOriginal, days, hours);
            generateHeatmap('newDataHeatmap', heatmapDataNew, days, hours);

            const originalHumidityData = {
                days: dates,
                humidity: weatherData.originalData?.forecast?.forecast.forecastday?.map(day => day.day.avghumidity) || [],
            };
    
            const newHumidityData = {
                days: dates,
                humidity: weatherData.newData?.forecast?.forecast.forecastday?.map(day => day.day.avghumidity) || [],
            };
    
            renderHumidityChart('originalDataHumidityChart', originalHumidityData, 'Original Data', chartInstances);
            renderHumidityChart('newDataHumidityChart', newHumidityData, 'New Data', chartInstances);
        }
    
        // Cleanup when the component unmounts
        return () => {
            Object.keys(chartInstances).forEach(key => {
                if (chartInstances[key]) {
                    chartInstances[key].destroy();
                    delete chartInstances[key];
                }
            });
        };
    }, [weatherData]);


    if (error) return <Typography color="error">Error: {error}</Typography>;
    if (!weatherData) return <Typography>Loading...</Typography>;

    const { originalData, newData } = weatherData;

    return (
        <Box p={2}>
            <Typography variant="h4" align="center" gutterBottom>
                Forecast Weather Dashboard
            </Typography>
            <Typography variant="subtitle1" align="center">
                Instance {originalData.instanceId} vs Instance {newData.instanceId}
            </Typography>
            <Typography variant="subtitle2" align="center">
                Last updated: {newData.createdDate || 'N/A'}
            </Typography>
            {/*Current weather cards*/}
            <Grid container spacing={3} mt={2} mb={4}>
                {/* Card 1: Original Data */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                textAlign: 'center',
                            }}
                        >
                            <Typography variant="h6" gutterBottom>
                                Instance {originalData.instanceId}
                            </Typography>
                            {originalData ? (
                                <>
                                    <Typography>
                                        <img
                                            src={originalData.current.current.condition.icon}
                                            alt="Weather Icon"
                                        />
                                    </Typography>
                                    <Typography>
                                        {originalData.current.current.temp_c}°C{' '}
                                        {originalData.current.current.condition.text}
                                    </Typography>
                                    <Typography>
                                        Measured Datetime: {originalData.current.location.localtime}
                                    </Typography>
                                </>
                            ) : (
                                <Typography>No data available</Typography>
                            )}
                        </CardContent>
                    </Card>
                </Grid>

                {/* Card 2: New Data */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                textAlign: 'center',
                            }}
                        >
                            <Typography variant="h6" gutterBottom>
                                Instance {newData.instanceId}
                            </Typography>
                            {newData ? (
                                <>
                                    <Typography>
                                        <img
                                            src={newData.current.current.condition.icon}
                                            alt="Weather Icon"
                                        />
                                    </Typography>
                                    <Typography>
                                        {newData.current.current.temp_c}°C{' '}
                                        {newData.current.current.condition.text}
                                    </Typography>
                                    <Typography>
                                        Measured Datetime: {newData.current.location.localtime}
                                    </Typography>
                                </>
                            ) : (
                                <Typography>No data available</Typography>
                            )}
                        </CardContent>
                    </Card>

                </Grid>
            </Grid>

            {/*3 day rain forecast*/}
            <Grid container mt={4} mb={4}>
                <Grid item xs={12} sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    textAlign: 'center',
                }}>
                    <Typography variant="h6" align="center" gutterBottom>Chance of Rain</Typography>
                    <Typography variant="body2" align="center" gutterBottom>
                                X-Axis: Days, Y-Axis: % Chance of Rain
                            </Typography>
                    <div id="weatherChart" style={{ height: '300px' }} />
                </Grid>
            </Grid>

            {/*3 Heatmap*/}

            <Grid container spacing={3} mt={4} mb={4}>
                {/* Card 1: Original Data Heatmap */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                textAlign: 'center',
                            }}>
                            <Typography variant="h6" align="center" gutterBottom>
                                Instance {originalData.instanceId} - Temperature Map
                            </Typography>
                            <Typography variant="body2" align="center" gutterBottom>
                                X-Axis: Days, Y-Axis: Hours, Colored by Temperature
                            </Typography>
                            <div id="originalDataHeatmap" />
                        </CardContent>
                    </Card>
                </Grid>

                {/* Card 2: New Data Heatmap */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                textAlign: 'center',
                            }}
                        >
                            <Typography variant="h6" align="center" gutterBottom>
                                Instance {newData.instanceId} - Temperature Map
                            </Typography>
                            <Typography variant="body2" align="center" gutterBottom>
                                X-Axis: Days, Y-Axis: Hours, Colored by Temperature
                            </Typography>
                            <div id="newDataHeatmap" />
                        </CardContent>
                    </Card>
                </Grid>


            </Grid>

            {/*Line Charts*/}
            <Grid container spacing={3}>

                {/* Line Chart Section */}
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent>
                            <Typography variant="h6" align="center" gutterBottom>
                            Instance {originalData.instanceId} - Humidity Trends
                            </Typography>
                            <Typography variant="body2" align="center" gutterBottom>
                                X-Axis: Days, Y-Axis: Humidity %
                            </Typography>
                            <canvas id="originalDataHumidityChart" style={{ height: '100px' }}></canvas>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={12} md={6}>
                    <Card>
                        <CardContent>
                            <Typography variant="h6" align="center" gutterBottom>
                            Instance {newData.instanceId} - Humidity Trends 
                            </Typography>
                            <Typography variant="body2" align="center" gutterBottom>
                                X-Axis: Days, Y-Axis: Humidity %
                            </Typography>
                            <canvas id="newDataHumidityChart" style={{ height: '100px' }}></canvas>
                        </CardContent>
                    </Card>
                </Grid>
                </Grid>
        </Box>
    );
};

export default Dashboard;
